import React, { useEffect } from 'react';
import Image from '../image';
import mapImage from "../../assets/map-icon.png";
import './map.css';

const Map = () => {
    const { kakao } = window;

    useEffect(() => { 
    var container = document.getElementById('map'); //   DOM 
    var x = 37.412729;
    var y = 127.127902;
    var options = { //     
        center: new kakao.maps.LatLng(x, y), // .
        level: 3 // (,  )
    };

    var map = new kakao.maps.Map(container, options); //    

    //  
    var marker = new kakao.maps.Marker({
        position: new kakao.maps.LatLng(x, y)
    });

    //     
    marker.setMap(map);

    //        
    var iwContent = '<div style="padding:5px;">W B1</div>', //    HTML  document element 
    iwRemoveable = true; // removeable  ture       x 

    //  
    var infowindow = new kakao.maps.InfoWindow({
    content : iwContent,
    removable : iwRemoveable
    });

    infowindow.open(map, marker);

    //   
    kakao.maps.event.addListener(marker, 'click', function() {
    //    
    infowindow.open(map, marker);  
    });

    }, []);

    return (
        <div width="100%" height="100%">
            <div className="text-icon-container"style={{paddingBottom: "30px"}} >
                <Image className="text-icon" src={mapImage}></Image>
                <span className="h3"> </span>
            </div>
            <div id="map" className="map">
            </div>
            <div className="h4">
                <span className="address">   925 16<br/><span className="line">1 W</span></span><br/>
                 : () 4  <br/><span className="line">-   1</span><br/>
                 : CGV  4F~7F <br/><span className="line">- 36   E/V  - 1</span>
            </div>
        </div>
    )
}

export default Map;